#gallerys{
    background-color: black;
    display: flex;
    flex-wrap: wrap;
    width:100vw;
    max-width:90rem;
    background-color:white;
}
.gallery{
    margin-left: 5rem;
    width:40vw;
}
.frame {
    margin-left: 5rem;
    border: 3px solid rgb(201, 199, 199);
    margin:1rem;
    height:20rem;
    width:32.3rem;
    position: relative;
    background-color: rgb(228, 224, 224);
}
.frameImg {
    border: 1px solid black;
    object-fit: cover;
    margin:1rem;
    height:35vh;
    width:35vw
}
.picTitle {
    font-weight: 600;
    text-align: center;
    width:10rem;
    position: absolute;
    top: 13.3rem;
    left:11.5rem;
    border-radius: 40rem;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 10px;
    z-index: 1;
}
.picDesc {
    justify-content: center;
    align-items: center;
    margin-top: 0.5rem;
    position:relative;
    text-align:center;
    color:black;
}

/* Media Query for screens smaller than 768px */
@media (max-width: 768px) {
    .gallery {
        margin-left: 5%;
        width: 90%;
    }

    .frame {
        margin-left: 5%;
        width: 90%;
    }

    .picTitle {
        width: 80%;
        top: 60%;
        left: 10%;
    }
}