#clients{
    height:75vh;
    width: 100vw;
    overflow: hidden;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:cover;
}
.clientTitle{
    font-size:2rem;
    align-items: center;
    justify-content:center;
    text-align: center;
    margin-top:3rem;
}
.clientDiv{
    margin: 1rem auto;
    width: 100vw;
    height: 56vh;
}
.clientImg{
    margin: 1rem;
    height:50vh;
    width: 20vw;
}
.marquee {
    white-space: nowrap; /* Prevent line breaks */
    overflow: hidden; /* Hide overflowing content */
    animation: marquee 1s linear infinite; /* Adjust duration as needed */
  }
  
  .marquee img {
    margin-right: 20px; /* Adjust spacing between images */
  }
  
  @keyframes marquee {
    0% {
        transform: translateX(100%); /* Start off-screen to the right */
    }
    100% {
        transform: translateX(-100%); /* Move off-screen to the left */
    }
  }

  /* Responsive styles for mobile devices */

@media screen and (max-width: 768px) {
    .clientTitle {
        font-size: 1.5rem; /* Adjust font size for smaller screens */
    }
    
    .clientDiv {
        height: auto; /* Remove fixed height on smaller screens */
    }
    
    .clientImg {
        height:auto;
        width:40vw; /* Adjust image width for smaller screens */
    }
}




