.navbar{
    opacity:90%;
    background-color: rgb(30, 30, 30);
    border-bottom: 0.5px solid white;
    height:4rem;
    width:87rem;
    margin:-2rem;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content:space-between ;
    position: sticky;
    top:0;
    z-index:3;
}
.logo{
    object-fit: cover;
    height:4rem;
    width:10rem;
}
.desktopMenuListItem{
    font-size:15px;
    margin: 0 1rem;
    cursor: pointer;

}
.desktopMenuListItem:hover{
    color:#FEE12B;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #FEE12B;
}
.desktopMenuBtn{
    height: 2rem;
    cursor: pointer;
    background:#FEE12B;
    color: black;
    display: flex;
    border: none;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}

/* Media query for screens up to 768px (typical mobile devices) */
@media (max-width: 768px) {
    .navbar {
        height: 3rem; /* Adjusted for mobile */
        padding: 0.5rem 1rem; /* Adjusted for mobile */
    }

    .logo {
        height: 3rem; /* Adjusted for mobile */
        width: 8rem; /* Adjusted for mobile */
    }

    .desktopMenuListItem {
        font-size: 12px; /* Adjusted for mobile */
        margin: 0 0.5rem; /* Adjusted for mobile */
    }

    .desktopMenuBtn {
        height: 1.5rem; /* Adjusted for mobile */
        padding: 0 0.5rem; /* Adjusted for mobile */
    }
}
