#footermain{
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:cover;
    margin-right: 2rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height:auto;
}
.LogoImg{
    margin-top:2rem;
    margin-left: 4rem;
}
.logoline1{
    color: rgb(143, 140, 140);
    margin-top: -0.7rem;
    margin-left: 2.6rem;
}
.logotext{
    color: rgb(143, 140, 140);
    margin-top: 0.5rem;
    margin-left: 1.8rem;
}
.logoline2{
    color: rgb(143, 140, 140);
    margin-top: -0.3rem;
    margin-left: 2.6rem;
}
.address{
    margin-top:2rem;
    color: rgb(143, 140, 140);
    margin-left: 7rem;

}
.addressText{
    padding-top:8px;
    color: rgb(143, 140, 140);
}
.list{
    margin-top:2rem;
    color: rgb(143, 140, 140);
    margin-left: 8rem;
}
li{
    padding:5px;
}
.copyright{
    color: rgb(143, 140, 140);
    margin-left: 9rem; 
    margin-top:3rem;
}
@media screen and (max-width: 768px) {
    #footermain {
        padding: 1rem; /* Reduce padding for smaller screens */
    }

    .LogoImg {
        margin-left: 0; /* Remove left margin on smaller screens */
    }

    .address, .list, .copyright {
        margin-left: 0; /* Remove left margin on smaller screens */
    }
}





