#certified{
    margin-top:-6rem;
    width: 100vw;
    max-width:80rem;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.certifiedImgs{
    background-color: white;
    margin-top:1.5rem;
    height:auto;
    width:100vw;
    max-width:90rem;
    
}
.certifiedImg{
    padding: auto;
    margin: 1rem;
    width:90vw;
    max-width: 100%;
}
.certifiedTitle{
    margin: 0.5rem;
    font-family:sans-serif;
}

/* Media query for mobile devices */
@media (max-width: 768px) {
    #certified {
        overflow: hidden;
        background-image: url(../../assets/bg.jpg);
        background-repeat:no-repeat;
        background-size: cover;
        margin-top:0rem; /* Adjust margin for smaller screens */
    }

    .certifiedImgs {
        max-width: 95%; /* Adjust max-width for smaller screens */
    }

    .certifiedImg {
        width: 95%; /* Adjust width for smaller screens */
    }

    .certifiedTitle {
        font-size: 1.2rem; /* Adjust font size for smaller screens */
    }
}